<template>
  <div name="testimonials">
    <CoverImage page="Testimonials" backgroundImage="backgnd_6.jpg" title="Testimonials" />

    <br />
    <br />
    <!-- Testimoials -->
    <v-container>
      <p class="display-0 text--disabled">Testimonials</p>
      <h2>
        <strong>Testimonials</strong>
        <font class="font-weight-light">&nbsp; from our valued guests</font>
      </h2>
      <v-layout row wrap justify-space-around class="ma-2 mp-2">
        <v-flex sm10 md5 v-for="(testimonial, i) in testimonials" :key="i">
          <v-card hover class="rounded-card" data-aos="flip-left">
            <v-list-item three-line>
              <v-list-item-content>
                <v-badge
                  avatar
                  overlap
                  bottom
                  offset-x="360"
                  offset-y="40"
                  class="d-flex align-self-start"
                  :icon="require('@/assets/images/testimonials/' + testimonial.image)"
                >
                  <v-list-item-avatar size="100" class="hidden-sm-and-down">
                    <img
                      :src="require('@/assets/images/testimonials/' + testimonial.image)"
                      :alt="testimonial.name"
                    />
                  </v-list-item-avatar>
                  <template v-slot:badge>
                    <v-icon size="24" color="red" class="hidden-sm-and-down">fa-quote-left</v-icon>
                  </template>
                </v-badge>
                <v-card-text class="display-0 text--secondary">
                  <p
                    class="pe-8"
                    style="text-align: justify; text-justify: inter-word;"
                  >{{testimonial.feedbackText}}</p>
                </v-card-text>
                <v-list-item-title class="mb-1 font-weight-bold">{{testimonial.name}}</v-list-item-title>
                <div class="overline mb-4">Guest from {{testimonial.location}}</div>
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>

    <br />
    <br />
  </div>
</template>

<script>
import CoverImage from "@/components/common/CoverImage.vue";
import testimonials from "@/components/mixins/testimonials.js";

export default {
  name: "Testimonials",
  head: {
    title: "Contact",
    link: [
      {
        rel: "canonical",
        href: "https://www.travelfreakvacations.com/testimonials"
      }
    ],
    meta: [
      {
        name: "description",
        content:
          "Check out what our repeat customers have to say about us. Connect with us, and we will take care of all your travel needs."
      },
      // OpenGraph data (Most widely used)
      { property: "og:title", content: "Testimonials - Travelfreak Vacations" },
      { property: "og:site_name", content: "Travelfreak Vacations" },
      // The list of types is available here: http://ogp.me/#types
      { property: "og:type", content: "website" },
      // Should be the same as your canonical link, see below.
      {
        property: "og:url",
        content: "https://www.travelfreakvacations.com/testimonials"
      },
      {
        property: "og:image",
        content: "https://www.travelfreakvacations.com/images/tfv-logo.png"
      },
      // Often the same as your meta description, but not always.
      {
        property: "og:description",
        content:
          "Check out what our repeat customers have to say about us. Connect with us, and we will take care of all your travel needs."
      }
    ]
  },
  components: {
    CoverImage
  },
  mixins: [testimonials]
};
</script>



